  
import React, { Component } from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import swal from "sweetalert";
import './style.css'
import { Col, Row, Container} from 'react-bootstrap'


const LoginSchema = Yup.object().shape({
  username: Yup.string()
    .min(2, "username is Too Short!")
    .max(50, "username is Too Long!")
    .required("Username is Required"),
  password: Yup.string().required("Password is required")
});


class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      alert: null
    };
  }
  componentDidMount() {
    if (localStorage.getItem("TOKEN_KEY") != null) {
      return this.props.history.push('/dashboard');
    }
      }
     

      submitForm = (values, history) => {
        values.username = values.username.toLowerCase()
        axios
          .post('/login', values, values)
          .then(res => {
            if (res.data.result === "success") {
              localStorage.setItem("TOKEN_KEY", res.data.token);
              localStorage.setItem("userName", values.username)
              
                this.props.history.push("/dashboard");
            } else if (res.data.result === "error") {
              swal("Error!", res.data.message, "error");
            }
          })
      .catch(error => {
        console.log(error);
        return swal("Error!", error.message, "error");
      });
  };
  showForm = ({
    values,
    errors,
    touched,
    handleChange,
    handleSubmit,
    setFieldValue,
    isSubmitting
  }) => {
    return (
      <Container fluid>
                <div  style={{width:"50%;"}}className="registerBox justify-content-md-center">
        <Row md={6} className="justify-content-center">
      <h1><b>Last</b>Play<span style={{fontSize:"14px"}}>.tv</span></h1>
      </Row>
      <Row md={12} className="justify-content-center">
      <img src={process.env.PUBLIC_URL + '/logo2.png' } alt="logo" /> 
            </Row>

  
        <Row md={6} className="justify-content-center">
        <form onSubmit={handleSubmit}>
        <div className="registerLogin">
          <input
            type="text"
            name="username"
            onChange={handleChange}
            value={values.username}
            className="form-control"
            placeholder="Username"
            className={
              errors.username && touched.username
                ? "form-control is-invalid"
                : "form-control"
            }
          />

          {errors.username && touched.username ? (
            <small id="passwordHelp" class="text-danger">
              {errors.username}
            </small>
          ) : null}
        </div>
        <div className="form-group input-group mb-3 has-feedback">
          <input
            type="password"
            name="password"
            onChange={handleChange}
            value={values.password}
            className="form-control"
            placeholder="Password"
            className={
              errors.password && touched.password
                ? "form-control is-invalid"
                : "form-control"
            }
          />
          
          {errors.password && touched.password ? (
            <small id="passwordHelp" class="text-danger">
              {errors.password}
            </small>
          ) : null}
        </div>
        
          <Col style={{ textAlign:"center"}}>
          <button
              type="submit"
              disabled={isSubmitting}
              className="btn btn-primary btn-block"
              style={{marginTop:'45px'}}
            >
              Login
            </button>
            <span style={{fontSize:"12px"}}>Forgot password? <br />Click here.</span>

          </Col>
      </form>         


        </Row>


        </div>

</Container>

    );
  };

  render() {
    return (
      <div>
<Container>
<Formik
                initialValues={{
                  username: "",
                  password: ""
                }}
                onSubmit={(values, { setSubmitting }) => {
                  this.submitForm(values, this.props.history);
                  setSubmitting(false);
                }}
                validationSchema={LoginSchema}
              >
              
                {props => this.showForm(props)}
              </Formik>
              </Container>

      </div>
      



            


    );
  }
}

export default Login;