import React, { Component } from 'react';
import axios from 'axios';
import { Col, Row, Container} from 'react-bootstrap'
import { Formik } from "formik";
import * as Yup from "yup";
import swal from "sweetalert";

const ContactSchema = Yup.object().shape({
    email: Yup.string()
      .required("Email address is required"),
    body: Yup.string().required("Email description is required"),
    username: Yup.string().required("Username not found.")
  });


class Contact extends Component {
    constructor(props) {
        super(props);
    
        this.state = {
          alert: null,
          loaded: false
        };
      }

    submitForm = (values, history) => {
        axios
          .post('/contact', values)
          .then(res => {
            console.log(res.data.result);
            if (res.data.result === "success") {
              swal("Success!", res.data.message, "success").then(value => {
                history.push("/contact");
              });
            } else if (res.data.result === "error") {
              swal("Error!", res.data.message, "error");
            }
          })
          .catch(error => {
            console.log(error);
            swal("Error!", "Unexpected error", "error");
          });
      };


    showForm = ({
        values,
        errors,
        handleChange,
        touched,
        handleSubmit,
        isSubmitting
      }) => {

    return(
        <div className="contactdiv">
             <img src={`https://i.redd.it/hi1bli2irmg41.jpg`} alt={`poster`} style={{width:'100%'}} />
        <Container style={{backgroundColor:"#eee", paddingLeft:"2%",paddingRight:"2%", paddingBottom:"7%"}}>
            
            <Row md={12} className="justify-content-md-center">
            <h3>Contact Us</h3>

            </Row>

            <Row>
                
<Col md={6} className="align-middle">
<p style={{marginTop:'7%'}}>We value loyal fans like you and our team wants to ensure that we are providing you with the 
highest quality of LastPlay's shows and content. While simultaneously providing a high standard in customer service. No detail goes unnoticed at LastPlay and this is thanks to your help!
If you have any questions or need assistance, please fill out the form and one of our customer support associates will be in touch with you soon. 
</p>
</Col>
<Col md={6} className="align-middle">
<form onSubmit={handleSubmit}>
      <div className="loginRegisterTitle">
            </div>
        <div className="emailfield">
          <input
            type="email"
            name="email"
            onChange={handleChange}
            value={values.email}
            className={
              errors.email && touched.email
                ? "form-control is-invalid"
                : "form-control"
            }
            placeholder="Email"
            style={{marginTop:'25px'}}

          />
          {errors.email && touched.email ? (
            <small id="passwordHelp" className="text-danger">
              {errors.email}
            </small>
          ) : null}
        </div>
        
        <div className="contactfield">
          <textarea rows="5"
            type="text"
            name="body"
            onChange={handleChange}
            value={values.body}
            className={
              errors.body && touched.body
                ? "form-control is-invalid"
                : "form-control"
            }
            placeholder="Send us a note here."
            style={{marginTop:'25px'}}

          />
          {errors.body && touched.body ? (
            <small id="bodyHelp" className="text-danger">
              {errors.body}
            </small>
          ) : null}
        </div>
        <div className="row">
          <div className="col s8 offset-s2 center">
            <button
              type="submit"
              disabled={isSubmitting}
              className="btn btn-primary"
              style={{marginTop:'25px'}}
            >
              Send
            </button>

          </div>
     
        </div>
      </form>
</Col>
</Row>
<span style={{fontStyle:"italic"}}>“Everything in this world is magic, except for the magician.” — Dr. Ford</span>
      </Container>
      </div>
    )
      }
       
          render() 
          {
              return(
                <div className="register-page">
                <div>
                  <Formik
                    initialValues={{
                      email: "",
                      body: "",
                      username: localStorage.userName
                    }}
                    onSubmit={(values, { setSubmitting }) => {
                      this.submitForm(values, this.props.history);
                      setSubmitting(false);
                    }}
                    validationSchema={ContactSchema}
                  >
                    {props => this.showForm(props)}
                  </Formik>
                </div>
                {/* /.form-box */}
              
              {/* /.card */}
            
          </div>
              )
          }
        }

export default Contact;