import React from 'react';
import './style.css';
import JSPlayer from './player'
import M from "materialize-css";
import {Tab, Col, Nav, Row, Container} from 'react-bootstrap'
import ListGroup from 'react-bootstrap/ListGroup'
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";


class Video extends React.Component {
    constructor(props) {
        super();
        this.displayData = [];

        this.state = {
            showdata: this.displayData,
            shows: [],
            episodes: [],
            player: '',
            poster: '',
            not_visible: true,
            loaded:false
        }
        

        this.appendData = this.appendData.bind(this);

    };
    componentDidMount() {
        let urls = [fetch(`https://lp-server1.lastplay.tv/app/api/episode?seriesId=${this.props.match.params.id}&apikey=33af4f32a34140faa3b3119541c78e14`),
                    fetch(`https://lp-server1.lastplay.tv/app/api/series/id=${this.props.match.params.id}?apikey=33af4f32a34140faa3b3119541c78e14`)]
        Promise.all(urls)
        .then(([res1, res2]) => { 
            return Promise.all([res1.json(), res2.json()]) 
         })
         .then(([res1, res2, res3]) => {
             res3 = res1.filter(res3 => res3.seasonNumber > 0)
           this.setState({
               shows: res2,
               episodes: res3,
               loaded: true,
           })
         });
         M.Tabs.init(this.Tabs);
   }

    appendData = (a) => {
           let link = 'https://lp-server1.lastplay.tv' + a.path.replace('/media/disk1', '')
           let pos = `https://lp-server1.lastplay.tv/app/MediaCover/${this.state.shows.id}/fanart.jpg`
           let epTitle = a
            this.setState({
                player: link,
                poster: pos,
                episodeTitle: epTitle,
                not_visible: false
        })
        this.useEffect()
    }

    useEffect = () => {
        window.scrollTo({
            top: 500,
            left: 0,
            behavior: 'smooth'
          });
      }

      useEffectBottom = () => {
        window.scrollTo({
            top: 1500,
            left: 0,
            behavior: 'smooth'
          });
        }


    render() {
        let i=0
        const responsive = {
            desktop: {
              breakpoint: { max: 3000, min: 600 },
              items: 3,
              partialVisibilityGutter: 40, // this is needed to tell the amount of px that should be visible.
              slidesToSlide: 3
            },
            tablet: {
              breakpoint: { max: 1024, min: 464 },
              items: 2,
              partialVisibilityGutter: 30,             // this is needed to tell the amount of px that should be visible.

              slidesToSlide: 2
            },
            mobile: {
              breakpoint: { max: 464, min: 0 },
              items: 1,
              partialVisibilityGutter: 30 // this is needed to tell the amount of px that should be visible.
            }
          }

        const style = this.state.not_visible ? {display: 'none', padding: '10%'} : {}
        
if (this.state.loaded === false) 
  return null;


        return ( 
            <div style={{backgroundColor:"#f1f1f1"}}>
                <div className="UpperHeader">
                 <img src={`https://lp-server1.lastplay.tv/app/MediaCover/${this.state.shows.id}/fanart.jpg`} alt={`${this.state.shows.title} poster`} style={{width:'100%'}} />
                </div>
<Container>
   <Row>
    <Col className="postertab" md={3} > 
        <img src={`https://lp-server1.lastplay.tv/app/MediaCover/${this.state.shows.id}/poster-250.jpg`} alt={`${this.state.shows.title} poster`} /> <br />
        <h2>{this.state.shows.title}</h2>
        <p>{this.state.shows.network} | {this.state.shows.runtime} min. | {this.state.shows.certification}</p>
        <span style={{fontSize:"14px"}}>{this.state.shows.genres.join(' | ')}</span>
    </Col>
    <Col md={9} style={{padding:'5%'}}>
        <p>{this.state.shows.overview}</p>
        
        <div className = "display-data-Container" style={style}  onContextMenu={(e) => {
    e.preventDefault(); 
    e.stopPropagation();
}}> 
            <h3>{this.state.show}</h3>
                    <JSPlayer source={this.state.player} poster={this.state.poster} >
                    </JSPlayer>
        </div> 
            <div>
            <Row>
      <Col xs={6}>
        
      </Col>
 
    </Row>
 
          <Tab.Container defaultActiveKey="season1">
            <Carousel responsive={responsive} centerMode={false}
              swipeable={false}
              draggable={false}
              ssr={true} // means to render carousel on server-side.
              infinite={true}
              keyBoardControl={true}
              containerClass="carousel-container"
              removeArrowOnDeviceType={["tablet", "mobile"]}
              deviceType={this.props.deviceType}
              dotListClass="custom-dot-list-style"
              itemClass="carousel-item-padding-40-px"
              focusOnSelect={true}
              >
        

                
                    {this.state.episodes.map(episode => {
                    if (this.state.shows.seasonCount > i) {
                        i++
                    return ( 
                        <div className="card" key={`${i}`}><Nav.Link eventKey={`season${i}`} >
                        <img variant="top" className="card-img" src={`http://lp-server1.lastplay.tv/t/${this.state.shows.imdbId}/season0${i}-poster.jpg`}
                         alt="poster" 
                         style={{backgroundImage: `url(https://lp-server1.lastplay.tv/app/MediaCover/${this.state.shows.id}/poster-250.jpg)`,
                          backgroundSize: 'cover'}}
                        onError={(e) => {
                            e.target.src = `https://lp-server1.lastplay.tv/app/MediaCover/${this.state.shows.id}/poster-250.jpg` // some replacement image
                         }} />
                        <h2 style={{maxHeight:'45px'}}>Season {i}  </h2>
                        </Nav.Link></div>
                                )}})}                  
                </Carousel>


      
                <ListGroup><Tab.Content className="seasonTabs" >
            {this.state.episodes.map(episode => {
                    if (episode.hasFile === false) {
                        return ( 
                            <Tab.Pane eventKey={`season${episode.seasonNumber}`} key={episode.id}><ListGroup.Item >
                                <span style={{color: 'red', fontStyle: 'italic'}} href={null}>{episode.episodeNumber}. <strong>{episode.title}</strong></span>
                                </ListGroup.Item></Tab.Pane>
                            
                          
                        )
                    }  else
                        return ( 
                            <Tab.Pane key={`tabs${i++}`} eventKey={`season${episode.seasonNumber}`} style={{overflow:'hidden'}} onClick = {this.appendData.bind(this, episode.episodeFile)} >
                                <span id={`season${episode.seasonNumber}`}style={{cursor: 'pointer'}} href={null}>
                                    <ListGroup.Item>
                                        <p>{episode.episodeNumber}. <strong>{episode.title}</strong> </p>
                                        <span style={{fontSize:"14px"}}>{episode.overview}</span>
                            </ListGroup.Item></span></Tab.Pane>
                            )
                        }
                  )}
                        </Tab.Content></ListGroup>
                    </Tab.Container>
                </div>
            </Col>
        </Row>
    </Container>



</div>
   )}
}

export default Video;