import React, { Component } from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import swal from "sweetalert";
import { Row, Container} from 'react-bootstrap'
import '../style.css'


const PasswordForgotSchema = Yup.object().shape({
  username: Yup.string()
    .required("username is Required")
});

class Passwordforgot extends Component {
  constructor(props) {
    super(props);

    this.state = {
      response: {},
      error_message: null,
      avatar: ""
    };
  }

  submitForm = formData => {
     axios
      .post("http://localhost:3000/password/reset", formData)
      .then(res => {
        console.log(res);
        if (res.data.result === "success") {
          swal("Success!", res.data.message, "success").then(value => {
            //s window.location.reload();
          });
        } else if (res.data.result === "error") {
          swal("Error!", res.data.message, "error");
        }
      })
      .catch(error => {
        console.log(error);
        swal("Error!", "Unexpected error", "error");
      });
  };
  showForm = ({
    values,
    errors,
    touched,
    handleChange,
    handleSubmit,
    onSubmit,
    isSubmitting,
    setFieldValue
  }) => {
    return (
      <div stlye={{backgroundColor:"#e3e3e3", minWidth:"100vh"}}>

      <div className="" style={{textAlign:"center"}}> 
      <h1><b>Last</b>Play<span style={{fontSize:"14px"}}>.tv</span></h1>
  <Row md={7} lg={4}>
  <Container style={{backgroundColor:"#fff"}}>
        <Row>
        <img src={process.env.PUBLIC_URL + '/logo2.png'} alt="logo" /> 
        </Row>
        <Row className="justify-content-center">
  


      <form onSubmit={handleSubmit}>
            <label htmlFor="username">Confirm your username and we will send you instructions to reset your password.</label>
            <input
              onChange={handleChange}
              value={values.username}
              type="username"
              className={
                errors.username && touched.username
                  ? "form-control is-invalid"
                  : "form-control"
              }
              id="username"
              placeholder="Username"
            />
            {errors.username && touched.username ? (
              <small id="passwordHelp" class="text-danger">
                {errors.username}
              </small>
            ) : null}
        {/* /.card-body */}
        <div class="row">
          <div class="col-12">
            <button
              type="submit"
              disable={isSubmitting}
              class="btn btn-primary"
            >
              Request new password
            </button>
          </div>
        </div>
      </form>
      </Row>
      </Container>
      </Row>
      </div>
      </div>
    );
  };

  render() {
    return (
<Container >
<Formik
                initialValues={{
                  username: ""
                }}
                onSubmit={(values, { setSubmitting }) => {
                  this.submitForm(values, this.props.history);
                  setSubmitting(false);
                }}
                validationSchema={PasswordForgotSchema}
              >
                {props => this.showForm(props)}
              </Formik>


</Container>





    );
  }
}

export default Passwordforgot;