import React, { Component } from 'react'
import swal from "sweetalert";
import { withRouter } from "react-router-dom";
import { Nav, Navbar, NavDropdown, Form, FormControl } from 'react-bootstrap'

class Header extends Component {
  Logout = () => {
    swal("Logout?", {
      buttons: {
        nope: {
          text: "Yes",
          value: "yes"
        },
        sure: {
          text: "Not yet",
          value: "no"
        }
      }
    }).then(value => {
      switch (value) {
        case "yes":
            localStorage.removeItem("TOKEN_KEY");
             this.props.history.push("/login");
          ;
          break;
        case "no":
          break;
        default:
 
      }
    
  })}

  render() {
  const username = localStorage.userName
    return (
<Navbar bg="dark" variant="dark" expand="lg">
<Navbar.Brand href="/dashboard"><img src={process.env.PUBLIC_URL + '/logo3.png'} alt="logo"/><b>Last</b>Play</Navbar.Brand>

        <Navbar.Toggle aria-controls="basic-navbar-nav" />


				<Navbar.Collapse id="basic-navbar-nav">
					<Nav className="mr-auto">
          {/*     <Form inline>
						  <FormControl type="text" placeholder="Search" className="mr-sm-2" />
				  </Form> */}
						<Nav.Link href="/dashboard">Home</Nav.Link>
            <Nav.Link href="/dashboard">TV</Nav.Link>
            <Nav.Link href="/movies" eventKey="disabled" disabled >Movies</Nav.Link>
          </Nav>
            <Nav>
            <Nav.Link href="/contact">Contact</Nav.Link>
            <NavDropdown title={`Welcome, ${username}`} id="collasible-nav-dropdown">
              <NavDropdown.Item bg="dark" href="#" onClick={this.Logout}>Logout</NavDropdown.Item>
        </NavDropdown>
          </Nav>
        </Navbar.Collapse>

			</Navbar>
            
    );
  }
}

export default withRouter(Header);


