import React from 'react';
import { Player, BigPlayButton } from 'video-react';
import "video-react/dist/video-react.css";

class JSPlayer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      videoTitle: '',
      autostart: 'viewable',
    };
    
    this.onReady = this.onReady.bind(this);
    this.onVideoLoad = this.onVideoLoad.bind(this);

    // each instance of <ReactJWPlayer> needs a unique id.
    // we randomly generate it here and assign to the container instance.
    this.playerId = ' ';

  }
  onReady(event) {
    // interact with JW Player API here
  }

  onVideoLoad(event) {
    this.setState({
      videoTitle: event.item.description // this only works with json feeds!
    });
  }
  render() 
  {
    return (
      <div className='video-react-big-play-button-center'>
        <h1>{ this.state.videoTitle }</h1>
        <Player
      playsInline = {false}
      poster={this.props.poster}
      src={this.props.source}
      className="video-react-big-play-button-center"

     
      
    ><BigPlayButton position="center" />
      </Player>

      </div>
    );
  }
}


export default JSPlayer;