import React, { Component } from 'react'
import { Link } from "react-router-dom";
import { Col, Row, Container} from 'react-bootstrap'
import '../style.css'


class Dashboard extends Component {
    constructor() {
        super()
        this.state = {
            shows: []

        }
    }

componentDidMount() {
this.forceUpdate()
    fetch('https://lp-server1.lastplay.tv/app/api/series?apikey=33af4f32a34140faa3b3119541c78e14',           
     { headers: {
        'Accept': 'application/json',
                  'Content-Type': ' application/json',
                  'X-API-SERVER': '85499f9f'
     }})
    .then(res => res.json())
    .then((data)=> {
        this.setState({ 
            shows: data.sort((a, b) => a.title > b.title)
        })


        
    })
    .catch(console.log)
}


render() 
{
    return (<div>

        <div>
          <Row style={{marginTop:"5%"}} className="justify-content-center">
            {this.state.shows.map(show =>
            <Col xs={6} sm className='shows' key={`${show.id}`}>
                <Link to={`tv/${show.id}`}>
                    <img src={`https://lp-server1.lastplay.tv/app/MediaCover/${show.id}/poster-250.jpg`} alt={`${show.title} poster`}/> <br />
                    <span>{show.title}</span>
                </Link>
            </Col>)}
          </Row>
        </div>
        </div>

    )
    }
}
export default Dashboard